
.tx-table{width:100%;border-collapse:collapse
}
.tx-table td{padding:8px 5px;white-space:nowrap
}
.tx-table .address-amount{width:100%;max-width:0
}
.tx-table .address-amount .address-amount-wrap{display:flex;white-space:nowrap
}
.tx-table .address-amount .address-amount-wrap .address{overflow:hidden;text-overflow:ellipsis;margin-right:5px;flex:1
}
.loading-wrap{text-align:center;margin:10px 0
}
.empty-state{color:#444;text-align:center
}
.status-pending{background:repeating-linear-gradient(45deg, transparent, transparent 11px, rgba(0,0,0,0.05) 11px, rgba(0,0,0,0.05) 22px)
}
