
#app{display:flex;flex-direction:column;overflow:hidden
}
#app .page-container{display:flex;flex-direction:column
}
#app>div{flex:1
}
#app>div:last-child{flex:0 0 auto
}
.seperator{background:linear-gradient(90deg, #4B64FF, #FF0097);height:1px
}
.seperator.top{height:4px
}
.box{background-color:#fff
}
.page{background-color:#fff;height:100%;overflow:auto;display:flex;flex-direction:column;flex:100% 0 0
}
.page>*{flex:0 0 auto
}
.page .account-subpage{flex:1;display:flex;overflow:hidden;word-break:break-all
}
.scroll-view{flex:1;overflow-y:auto;position:relative
}
.scroll-view::-webkit-scrollbar{width:6px
}
.scroll-view:hover::-webkit-scrollbar{background-color:transparent;width:6px
}
.scroll-view:hover::-webkit-scrollbar-thumb{background-color:rgba(0,0,0,0.2)
}
.form{margin:10px
}
.form .form-line{padding:5px 3px;border-bottom:1px solid #DFDFDF
}
.form .form-line:last-of-type{border-bottom:0
}
.form .form-line label{font-weight:500;display:flex;justify-content:space-between;align-items:center
}
.form .form-line.vertical label{flex-direction:column;align-items:stretch
}
.form .form-line.vertical label .text-input{margin-top:5px
}
.form-actions{display:flex;flex-direction:column;align-items:center;padding:10px 0 0
}
.text-input{border:1px solid #C6C6C6;border-radius:4px;line-height:20px;padding:0 6px;font-weight:normal
}
.text-input[disabled]{background-color:#f6f6f6
}
h2,strong{font-weight:500;font-size:1em
}
h2{margin:15px 0 10px
}
p{line-height:1.25
}
.label{display:inline-block;vertical-align:text-bottom;text-transform:uppercase;font-size:.85em;background-color:#eee;line-height:1.5;padding:0 .4em;border-radius:4px
}
.label.label-positive{background-color:#13c329;color:#fff
}
.label.label-negative{background-color:#f57336;color:#fff
}
.label.label-action{font-weight:500
}
.formatted-value{white-space:nowrap
}
.formatted-value.token{background-color:#f0f0f0;padding:0 3px
}
.formatted-value .value{font-weight:500
}
.formatted-value .sep{display:inline-block;width:3px
}
.formatted-value .point{display:inline-block
}
.overlay-dialog{background:#fff;position:absolute;top:0;left:0;right:0;bottom:0;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;padding:20px;text-align:center;opacity:0;transition:opacity .3s;pointer-events:none
}
.overlay-dialog.visible{opacity:1;pointer-events:all
}
.overlay-dialog p{overflow-wrap:break-word;max-width:100%
}
.icon,.logo{display:inline-block;vertical-align:text-bottom;width:12px;height:12px;background-repeat:no-repeat;background-size:contain;text-indent:-10000px
}
.icon.icon-view,.logo.icon-view{background-image:url(data:image/svg+xml;base64,)
}
.icon.icon-success,.logo.icon-success{width:32px;height:32px;background-image:url(data:image/svg+xml;base64,)
}
.icon.icon-fail,.logo.icon-fail{width:32px;height:32px;background-image:url(data:image/svg+xml;base64,)
}
.icon.icon-lock,.logo.icon-lock{width:25px;height:34px;background-image:url(data:image/svg+xml;base64,)
}
.icon.icon-aergo,.logo.icon-aergo{width:34px;height:34px;background-image:url(data:image/svg+xml;base64,)
}
.icon.icon-warning,.logo.icon-warning{width:38px;height:34px;background-image:url(data:image/svg+xml;base64,)
}
.icon.icon-export,.logo.icon-export{width:25px;height:29px;background-image:url(data:image/svg+xml;base64,)
}
.logo{width:108px;height:46px;background-image:url(data:image/svg+xml;base64,)
}
