
.account-nav{font-size:1.08333em;display:flex;list-style:none;margin:10px 0;padding:0;justify-content:stretch
}
.account-nav li{flex:1
}
.account-nav li a{display:block;text-align:center;text-decoration:none;color:inherit;text-transform:uppercase
}
.account-nav li a span{border-bottom:1px solid transparent;padding-bottom:2px
}
.account-nav li a.router-link-exact-active span{border-color:#FF36AD
}
.account-header{overflow:auto;padding:12px 15px;display:flex;align-items:center
}
.account-header .account-item{flex:1
}
.menu-link{width:24px;height:24px;background:url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTYiIGhlaWdodD0iMTQiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHBhdGggZD0iTTAgMWgxNk0wIDdoMTZNMCAxM2gxNiIgc3Ryb2tlPSIjRkYzNkFEIiBzdHJva2Utd2lkdGg9IjIiLz48L3N2Zz4=);background-size:16px 14px;background-repeat:no-repeat;background-position:center center
}
.account-name{font-weight:500
}
.account-item{line-height:15px;display:flex
}
.account-item>:nth-child(2){flex:1
}
.account-item .identicon{padding:2px;border:1px solid #aaa;border-radius:8px;margin-right:7px
}
.account-item .identicon svg{display:block;width:26px;height:26px
}
