
.account-list-header{text-align:center;color:#FF0097;font-size:.91667em;line-height:22px;border-bottom:1px solid #DFDFDF
}
.account-list{margin:0;padding:0;list-style:none
}
.account-list>li{border-bottom:1px solid #DFDFDF
}
.account-list>li a{display:block;text-decoration:none;color:inherit;padding:12px 15px
}
.account-list>li:hover{background-color:#f7f7f7
}
.account-item .btn-action{visibility:hidden;float:right;width:25px;height:32px;background:url(data:image/svg+xml;base64,) 50% 50%;background-repeat:no-repeat;background-size:5px auto
}
.account-item .btn-action:hover{background-image:url(data:image/svg+xml;base64,)
}
.account-item{position:relative
}
.account-menu{position:absolute;top:0;right:10px;display:none;pointer-events:none
}
.account-menu ul{display:block;margin:0;padding:0;background-color:#eee;border-radius:5px;list-style:none;border:1px solid #ddd
}
.account-menu ul li{border-bottom:1px solid #ddd;line-height:2.5em;padding:0 .75em;white-space:nowrap
}
.account-menu ul li:last-child{border-bottom:0
}
.account-menu ul li:hover{background-color:#e0e0e0
}
.account-list li:hover .btn-action{visibility:visible
}
.account-list li:hover .account-menu.visible{display:block;pointer-events:all;z-index:1
}
.add-account{color:#FF0097;background:url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMjMiIGhlaWdodD0iMjMiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHBhdGggZD0iTTAgMTEuNWgyM00xMS41IDB2MjMiIHN0cm9rZT0iI0ZGMzZBRCIgc3Ryb2tlLXdpZHRoPSI1IiBmaWxsPSJub25lIi8+PC9zdmc+) 4px 50%;background-size:23px 23px;background-repeat:no-repeat;padding-left:38px
}
.add-account.account-item{line-height:23px
}
.chain{color:#444
}
.chain:before{content:"";width:10px;height:10px;display:inline-block;background:url(data:image/svg+xml;base64,) 50% 50%;background-size:contain;margin-right:2px;vertical-align:text-bottom;-webkit-transform:translateY(-0.15em);transform:translateY(-0.15em)
}
