
.network-selector{font-size:75%;text-transform:uppercase;font-weight:500
}
.network-icon{display:inline-block;vertical-align:text-bottom;width:9px;height:9px;margin-right:2px;-webkit-transform:translateY(-1px);transform:translateY(-1px);background:url(data:image/svg+xml;base64,) 50% 50% no-repeat
}
.network-icon.network-ok{background-image:url(data:image/svg+xml;base64,)
}
.network-icon.network-fail{background-image:url(data:image/svg+xml;base64,)
}
