
.dialog{background-image:linear-gradient(90deg, #4B64FF, #FF0097);position:absolute;top:0;left:0;width:100%;min-height:100%;display:flex
}
.dialog .dialog-content{flex:1;margin:12px;padding:15px;border-radius:4px;background-color:#fff
}
.dialog .dialog-content h1{font-size:16px;font-weight:normal
}
.dialog .dialog-content h1,.dialog .dialog-content p{text-align:center
}
